<template>
  <div class="autoRenew">
    <van-cell-group>
      <van-cell title="自动续费">
        <span :class="{warn:renew==0}">{{renew==0?'关':'开'}}</span>
      </van-cell>
      <van-cell title="扣款日期">
        <span>每月{{billDay}}日</span>
      </van-cell>
    </van-cell-group>
    <van-button @click="comfirmDialog" :class="{activeBtn:renew==0}">{{renew==0?'开启自动续费':'关闭自动续费'}}</van-button>
  </div>
</template>

<script>
import { autoRenew } from '@/api/index.js';

export default {
  name: 'autoRenew',
  data() {
    return {
      showDialog: false,
      renew: this.$route.params.renew,
      billDay: this.$route.params.billDay,
    };
  },
  created() {

  },
  computed: {},
  methods: {
    changeAutoRenew() {
      autoRenew(this.$route.params.orderId, this.renew === '1' ? 0 : 1)
        .then(() => {
          this.$toast('操作成功！');
          this.$router.go(-1);
        })
        .catch(err => console.error(err));
    },
    comfirmDialog() {
      this.$dialog
        .confirm({
          message:
            this.renew === '1' ? '您确定要关闭自动续费吗？关闭后将无法再次开启，此操作会影响您接下来的课程学习。' : '重新开启自动续费后，将扣除本期的学习费用，请确保卡内余额充足。',
          cancelButtonText: this.renew === '1' ? '继续关闭' : '取消',
          confirmButtonText: this.renew === '1' ? '暂不' : '开启',
        })
        .then(() => {
          if (this.renew === '0') {
            this.changeAutoRenew();
          }
        })
        .catch(() => {
          if (this.renew === '1') {
            this.changeAutoRenew();
          }
          // 继续关闭
        });
    },
  },
  components: {},
};
</script>
